<template>
  <div class="slide-show" @mouseover="clearInt()" @mouseout="runInt()" >
    <div class="slide-img">
      <a>
          <img :src='slides[nowIndex].src'/>
      </a>
    </div>
    <h2>{{slides[nowIndex].title}}</h2>
    <ul class="slide-pages">
      <li @click="goto(preIndex)">&lt;</li>
      <li v-for="(item,index) in slides">
        <a @click="goto(index)" :class="{'on':index==nowIndex}">{{index+1}}</a>
      </li>
      <li @click="goto(nextIndex)">&gt;</li>
    </ul>
  </div>
</template>

<script>
export default {
	mounted(){
		this.runInt()
	},
	props:{
		//这里的数据也算是data里的数据
		slides:{
			type:Array,
			default:[]
		},
		inv:{
			type:Number,
			default:1000
		}
	},
	computed:{
		//这里的数据也算是data里的数据
		preIndex(){
			if(this.nowIndex==0){
				return this.slides.length-1
			}else{
				return this.nowIndex-1
			}
		},
		nextIndex(){
			if(this.nowIndex==this.slides.length-1){
				return 0;
			}else{
				return this.nowIndex+1;
			}
		}
		
	},
	data(){
		return {
			nowIndex:0
		}
	},
	methods:{
		goto(index){
			this.nowIndex=index;
		},
		runInt(){
			this.intId=setInterval(()=>{
				this.goto(this.nextIndex);
			},this.inv)
		},
		clearInt(){
			clearInterval(this.intId)
		}
	}
/*  props: {
    slides: {
      type: Array,
      default: []
    },
    inv: {
      type: Number,
      default: 1000
    }
  },
  data () {
    return {
      nowIndex: 0,
      isShow: true
    }
  },
  computed: {
    prevIndex () {
      if (this.nowIndex === 0) {
        return this.slides.length - 1
      }
      else {
        return this.nowIndex - 1
      } 
    },
    nextIndex () {
      if (this.nowIndex === this.slides.length - 1) {
        return 0
      }
      else {
        return this.nowIndex + 1
      }
    }
  },
  methods: {
    goto (index) {
      this.isShow = false
      setTimeout(() => {
        this.isShow = true
        this.nowIndex = index
      }, 10)
    },
    runInv () {
      this.invId = setInterval(() => {
        this.goto(this.nextIndex)
      }, this.inv)
    },
    clearInv () {
      clearInterval(this.invId)
    }
  },
  mounted () {
    this.runInv();
  }*/
}
</script>

<style scoped>
.slide-trans-enter-active {
  transition: all .5s;
}
.slide-trans-enter {
  transform: translateX(900px);
}
.slide-trans-old-leave-active {
  transition: all .5s;
  transform: translateX(-900px);
}
.slide-show {
  position: relative;
  margin: 15px 15px 15px 0;
  width: 900px;
  height: 500px;
  overflow: hidden;
}
.slide-show h2 {
  position: absolute;
  width: 100%;
  height: 100%;
  color: #fff;
  background: #000;
  opacity: .5;
  bottom: 0;
  height: 30px;
  text-align: left;
  padding-left: 15px;
}
.slide-img {
  width: 100%;
}
.slide-img img {
  width: 100%;
  position: absolute;
  top: 0;
}
.slide-pages {
  position: absolute;
  bottom: 10px;
  right: 15px;
}
.slide-pages li {
  display: inline-block;
  padding: 0 10px;
  cursor: pointer;
  color: #fff;
}
.slide-pages li .on {
  text-decoration: underline;
}
</style>



